<template>
  <v-container>

    <!--      -->
    <v-btn
      fab
      fixed
      bottom
      right
      color="primary"
      dark
      @click="addClickBtn"
      style="box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>

    <!--      () -->
    <v-btn
      fab
      fixed
      bottom
      right
      color="error"
      dark
      @click="deleteAll"
      style="margin-right: 70px; box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);"
    >
      <v-icon>mdi-delete</v-icon>
    </v-btn>

    <!--    -->
    <v-dialog v-model="add_dialog" max-width="500px">
      <v-card elevation="2" class="rounded-xl">
        <v-card-title class="headline primary--text"> </v-card-title>
        <v-card-text>
          <v-text-field v-model="add_title" label="" outlined></v-text-field>
          <v-text-field readonly v-model="add_writer" label="" outlined></v-text-field>
          <v-textarea v-model="add_content" label="" outlined></v-textarea>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" text @click="add_dialog = false"></v-btn>
          <v-btn color="primary" dark @click="addNotice"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--    -->
    <v-dialog v-model="update_dialog" max-width="500px">
      <v-card elevation="2" class="rounded-xl">
        <v-card-title class="headline primary--text"> </v-card-title>
        <v-card-text>
          <v-text-field v-model="update_title" label="" outlined></v-text-field>
          <v-text-field readonly v-model="update_writer" label="" outlined></v-text-field>
          <v-textarea v-model="update_content" label="" outlined></v-textarea>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" text @click="update_dialog = false"></v-btn>
          <v-btn color="primary" dark @click="updateNotice"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- :    -->
    <v-snackbar v-model="snackbar" :timeout="snackbarTimeout" top>
      {{ snackbarMessage }}
    </v-snackbar>

    <!--   -->
    <v-dialog v-model="isLoading" persistent hide-overlay width="300">
      <v-card color="#FFC107" dark class="rounded-xl">
        <v-card-text>
          <div class="text-center">
            <h3>{{ isLoadingMessage }}</h3>
          </div>
          <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>

    <!--  ,   -->
    <div v-if="isLoading" class="custom-overlay"></div>

    <!--     -->
    <v-row>

      <!--        -->
  <v-col cols="12" class="d-flex align-center" style="position: relative;">
    <v-btn icon @click="BackClick">
      <v-icon>mdi-arrow-left</v-icon>
    </v-btn>
  </v-col>

      <v-col>
        <v-card class="pa-5 notice-card rounded-xl" outlined>
          <v-card-title class="primary--text">
            <v-icon left>mdi-bell-ring-outline</v-icon>
             
          </v-card-title>
          <v-card-text>
            <div v-if="recent_id">
              <h2 class="title">{{ recent_title }}</h2>
              <p>{{ recent_content }}</p>
              <p class="writer">: {{ recent_writer }}</p>
              <p class="time"> : {{ recent_time }}</p>
            </div>
            <div v-else>
              <p>  .</p>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <!--    -->
    <v-row>
      <v-col>
        <v-card class="pa-5 notice-card rounded-xl" outlined>
          <v-card-title class="primary--text">
              
            <!--   -->
            <v-btn icon @click="updateClickBtn">
              <v-icon>mdi-pencil</v-icon>
            </v-btn>
            <!--   -->
            <v-btn icon @click="deleteNotice">
              <v-icon color="red">mdi-delete</v-icon>
            </v-btn>
          </v-card-title>
          <v-card-text>
            <div v-if="select_id">
              <h2 class="title">{{ select_title }}</h2>
              <p>{{ select_content }}</p>
              <p class="writer">: {{ select_writer }}</p>
              <p class="time">: {{ select_time }}</p>
            </div>
            <div v-else>
              <p>   .</p>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <!--      -->
    <v-card class="mt-5 pa-5 rounded-xl" elevation="3" outlined style="border-radius: 12px;">
      <v-card-title class="primary--text"> </v-card-title>

      <!--      -->
      <v-row class="mt-5" align="center" justify="start">
        <!--    -->
        <v-col cols="auto">
          <v-menu offset-y transition="scale-transition" bottom min-width="200">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="success" v-bind="attrs" v-on="on" large class="rounded-xl">
                {{ searchOption }}
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="selectOption('')">
                <v-list-item-title></v-list-item-title>
              </v-list-item>
              <v-list-item @click="selectOption('')">
                <v-list-item-title></v-list-item-title>
              </v-list-item>
              <v-list-item @click="selectOption('')">
                <v-list-item-title></v-list-item-title>
              </v-list-item>
              <v-list-item @click="selectOption('')">
                <v-list-item-title></v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-col>

        <!--    -->
        <v-col cols="5">
          <v-text-field
            v-model="search_text"
            label=" ."
            outlined
            dense
            hide-details
            class="rounded-xl"
          ></v-text-field>
        </v-col>

        <!--   -->
        <v-col cols="auto">
          <v-btn color="primary" large @click="searchBtn(1)" class="rounded-xl">
            
          </v-btn>
        </v-col>
      </v-row>

      <!--   -->
      <v-list class="mt-4">
        <v-list-item
          v-for="notice in notices"
          :key="notice.time"
          @click="selectNotice(notice)"
          class="notice-list-item"
          style="border-bottom: 1px solid #eee; padding: 10px 0;"
        >
          <v-list-item-content>
            <v-list-item-title>
              <strong>{{ notice.title }}</strong>
            </v-list-item-title>
            <v-list-item-subtitle>{{ notice.writer }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <!--     -->
      <v-row justify="center" align="center" class="mt-4">
        <!--    -->
        <v-btn
          :disabled="current_button_Page === 1"
          @click="prevPage"
          class="nav-btn"
          outlined
        >
          
        </v-btn>

        <v-btn
          v-for="n in currentButtons"
          :key="n"
          @click="getByNotice(n)"
          class="photo-button"
          :color="activeButton === n ? 'success' : 'primary'"
          outlined
          style="margin-left: 5px; margin-right: 5px;"
        >
          {{ n }}
        </v-btn>

        <!--    -->
        <v-btn
          :disabled="current_button_Page === totalButtonPages"
          @click="nextPage"
          class="nav-btn"
          outlined
        >
          
        </v-btn>
      </v-row>
    </v-card>
  </v-container>
</template>



<script>
export default {
  data(){
    return {
      //  
      snackbar: false, //   
      snackbarMessage: '',
      snackbarTimeout: 4000, //   

      isLoading: false, //   
      isLoadingMessage: '', //  

      loadingTimer:null, //  

      //   
      now_time:null,

      add_dialog: false, //     

      //    
      add_title: '',
      add_content: '',
      add_writer:'',
      add_time:'',

      //   
      notices : [],

      //    
      select_id: '',
      select_title: '',
      select_content: '',
      select_writer:'',
      select_time:'',

      //    
      update_dialog: false,

      //    
      update_id: '',
      update_title: '',
      update_content: '',
      update_writer:'',
      update_time:'',

      //    
      delete_id: '',

      //      
      recent_id: '',
      recent_title: '',
      recent_writer: '',
      recent_content: '',
      recent_time: '',
      

      current_button_Page: 1, //   
      set_button_page : 5, //     
      totalButton: null, //   

      activeButton:null, //       
      
      searchOption: '', //    
      searchText:'', //  
      search_title: '', //  ( )
      search_writer: '', //  ( )
      search_content: '', //  ( ),

      search_text: '', // 

      search_status:false, //   

      
    }
  },
  

  methods :{
    //    
    generateTime(){
      this.now_time = new Date() //      
      this.now_time.setHours(this.now_time.getHours() + 9) // UTC -   9  
      this.now_time = this.now_time.toISOString().slice(0, 19).replace('T', ' ')  //       
    },

    //        
    addClickBtn(){
      this.add_dialog = true;
      this.add_title = '';
      this.add_content = '';
      this.add_writer = localStorage.getItem("userId");
    },

    //   
    async addNotice() {

      //  ( )
      if (!this.add_title.trim()){
        this.$toast.error(" .")
        return;
      }
      if (!this.add_writer.trim()){
        this.$toast.error(" .")
        return;
      }
      if (!this.add_content.trim()){
        this.toast.error(" .")
        return;
      }

      //    
      this.generateTime();  //          
      
      // http   ( )
      const addNotice = {
        title : this.add_title,
        content : this.add_content,
        writer : this.add_writer,
        time : this.now_time
      };


      try{

        // 3      
        this.loadingTimer = setTimeout(() => {
          this.isLoading = true;
          this.isLoadingMessage = "  .."
        }, 3000)


        //   
        await this.$axios.post('/api/notice/add', addNotice);
        
        //    3    
        clearTimeout(this.loadingTimer);

        //  3  clearTimeout          
        this.isLoading = false;

        //   
        this.getByNotice(1);

        //   
        this.$toast.success(" .");

        //    
        this.add_dialog = false;
      }
      
      //    
      catch(error){
        this.$toast.error("   .  .");
      }
    },

    //      (   )
    selectNotice(notice){
      this.select_id = notice.id;
      this.select_title = notice.title;
      this.select_writer = notice.writer;
      this.select_content = notice.content;
      this.select_time = notice.time;
    },

    //     (  )
    updateClickBtn(){
      if(!this.select_id){
        this.$toast.info("     .");
        return;
      }

      if(this.select_writer !== localStorage.getItem('userId')){
      this.$toast.error("   .");
      return;
    }
      
      //   
      this.update_dialog = true;

      //       
      this.update_id = this.select_id;
      this.update_title = this.select_title;
      this.update_writer = this.select_writer;
      this.update_content = this.select_content;
      this.update_time = this.select_time;
    },

    //  
    async updateNotice() {


    //  ( )
    if (!this.update_title.trim()){
      this.$toast.error(" .");
      return;
    }
    if (!this.update_content.trim()){
      this.$toast.error(" .");
      return;
    }

    //    
    this.generateTime();  //          

    // http   ( )
    const updateNotice = {
      id : this.update_id,
      title : this.update_title,
      content : this.update_content,
      time : this.now_time
    };


try{

  // 3      
  this.loadingTimer = setTimeout(() => {
    this.isLoading = true;
    this.isLoadingMessage = "  .."
  }, 3000)


  //   
  await this.$axios.post('/api/notice/update', updateNotice);
  
  //    3    
  clearTimeout(this.loadingTimer);

  //  3  clearTimeout          
  this.isLoading = false;

  //   
  await this.getByNotice(1);

  //    
  await this.recentNotice();

  //   
  this.$toast.success(" .");

  //     
  this.selectNotice(this.notices[0])


  //    
  this.update_dialog = false;
}

//    
catch(error){
  this.$toast.error("   .  .");
}
},
    
//  
async deleteNotice() {

//    
this.delete_id = this.select_id

//      
if(!this.delete_id){
  this.$toast.info("     .");
  return;
}

//       
else if(this.select_writer !== localStorage.getItem("userId")){
  this.$toast.error("   .");
  return;
}

// http   ( )
const deleteNotice = {
  id : this.delete_id
}

try{

// 3      
this.loadingTimer = setTimeout(() => {
this.isLoading = true;
this.isLoadingMessage = "  .."
}, 3000)


//   
await this.$axios.post('/api/notice/delete', deleteNotice);

//    3    
clearTimeout(this.loadingTimer);

//  3  clearTimeout          
this.isLoading = false;

//   
await this.getByNotice(1);

//   
this.$toast.success(" .");

//    
this.select_id = ''


//    
this.update_dialog = false;
}

//    
catch(error){
this.$toast.error("   .  .");
}
},

//  
async deleteAll(){

  //  
  const USER = {
    writer : localStorage.getItem('userId')
  }

    await this.$axios.post('/api/notice/deleteAll', USER)
    await this.getByNotice(1);
    this.$toast.success("    .");
    //    
    this.select_id = ''

  },
  //      
  async recentNotice(){

    if (this.notices.length === 0){
    this.recent_id = '';
    this.recent_title = '';
    this.recent_writer = '';
    this.recent_content = '';
    this.recent_time = '';
    return;
    }

    
    this.recent_id = this.notices[0].id;
    this.recent_title = this.notices[0].title;
    this.recent_writer = this.notices[0].writer;
    this.recent_content = this.notices[0].content;
    this.recent_time = this.notices[0].time;

   
  },


    //   
    prevPage() {

      //     1    
      if (this.current_button_Page > 1) {
        this.current_button_Page--; //    
        const start = (this.current_button_Page - 1) * this.set_button_page + 1; //       
        this.getByNotice(start) //     
      }
    },


    //   
    async nextPage() {
      //           
      if (this.current_button_Page < this.totalButtonPages) {
        this.current_button_Page++; //    
        const start = (this.current_button_Page - 1) * this.set_button_page + 1; //       
        this.getByNotice(start); //       
      }
    },


    //      
    async countNotice(){

    //    
    const response = await this.$axios.get("/api/notice/count")

    //    /      =   
    this.totalButton = Math.ceil(response.data / 10)

    },


    //     
    async getByNotice(n) {

      //    
      this.activeButton = n;

      // console.log(` ${n} .`);
      
      const start = (n - 1) * 10 + 1 //      

      if (this.search_status){
        this.searchBtn(n)
        return;
      }

      //    
      await this.countNotice();
      

      try{
        const response = await this.$axios.post('/api/notice/getByStart', { start });
        this.notices = response.data

        //      
        await this.recentNotice();

      }catch(error){
        this.$toast.error("   .  .");

         //      
         await this.recentNotice();
        return;
      }
      
    },

    //   
    selectOption(option){

      //   
      this.search_status = false;

      this.search_text = '';

    
      if(option === ""){
        //   
        this.getByNotice(1)

        //   
        this.search_status = false;
        
        this.$toast.success("  .");

        this.searchOption = option;
        return;
      }

      //      
      this.searchOption = option;


      

    },

    //   ( )
    async searchBtn(n){

      if(this.searchOption === " "){
        this.$toast.error("  .");
        return;
      }

      if(this.searchOption === ""){
        this.$toast.error("  .");
        return;
      }

      //   
      const searchOption = {
        option: this.searchOption,
        text: this.search_text
      }


      //    
      this.activeButton = n;

      try{
        //    (     EndPoint)
        const response = await this.$axios.post("/api/notice/searchCount", searchOption)
        //    /      =   
        this.totalButton = Math.ceil(response.data / 10) 
      
      } catch(error){
        this.$toast.error("  .");
        return;
      }

      const start = (n - 1) * 10 + 1 //      

      // 2 
      const searchOption2 = {
        option: this.searchOption,
        text: this.search_text,
        start: start
      }

      try{
        //    http 
        const response = await this.$axios.post("/api/notice/searchNotice", searchOption2)
        this.notices = response.data;
        
        this.$toast.success('.');

        //   
        this.search_status = true;
      }catch(error){
        this.$toast.error("  .");
        return;
      }
    },

    BackClick(){
      //  
      this.$router.push('/home');
    }

},























  //      
  created(){
    this.getByNotice(1); //   
    this.selectOption(" ")
    this.$checkToken();
  },
  computed: {
    //     
    currentButtons() {
      const start = (this.current_button_Page - 1) * this.set_button_page + 1;
      return Array.from({ length: this.set_button_page }, (_, i) => start + i).filter(n => n <= this.totalButton);
    },
    //    
    totalButtonPages() {
      //    /       =    
      return Math.ceil(this.totalButton / this.set_button_page);
    }
  },
}


</script>

<style scoped>
.v-btn {
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

.v-card-title {
  font-weight: bold;
  font-size: 18px;
}

.title {
  font-size: 22px;
  font-weight: bold;
}

.writer, .time {
  font-size: 14px;
  color: #757575;
}

.notice-card {
  background-color: #F5F5F5;
  border-radius: 12px;
}

.notice-list-item {
  transition: background-color 0.3s ease;
}

.notice-list-item:hover {
  background-color: #e0f7fa;
}

.custom-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}.photo-buttons {
  text-align: center;
  margin-top: 50px;
}

.photo-button {
  margin: 5px;
}

.pagination-controls {
  margin-top: 20px;
}

.nav-btn {
  margin: 5px;
}
</style>
