<template>
    <v-container class="d-flex justify-center align-center" style="min-height: 100vh; background-color: #f0f4f8;">
      <v-card class="pa-8" max-width="500">
        <v-card-title class="text-h5 text-center font-weight-bold"></v-card-title>
        <v-card-text>
          <v-form ref="form" lazy-validation>
            <!--   -->
            <v-text-field
              v-model="userId"
              label=""
              outlined
              clearable
              dense
              @keydown.enter="login"
              class="mt-6"
              :rules="[rules.required, rules.minLength]"
              style="font-size: 18px;"
            ></v-text-field>
  
            <!--   -->
            <v-text-field
              v-model="password"
              label=""
              outlined
              clearable
              dense
              @keydown.enter="login"
              class="mt-6"
              :type="showPassword ? 'text' : 'password'"
              :rules="[rules.required, rules.minLength]"
              style="font-size: 18px;"
            >
              <template #append>
                <v-btn
                  icon
                  @click="showPassword = !showPassword"
                >
                  <v-icon large>{{ showPassword ? 'mdi-eye-off' : 'mdi-eye' }}</v-icon>
                </v-btn>
              </template>
            </v-text-field>
          </v-form>
        </v-card-text>
  
        <!--   -->
          <v-btn
            color="primary"
            block
            class="mb-4"
            style="font-size: 18px; padding: 12px;"
            @click="login"
          >
            
          </v-btn>
          <v-btn
            color="success"
            block
            class="mb-4"
            style="font-size: 18px; padding: 12px;"
            @click="signUp"
          >
            
          </v-btn>
          <v-btn
            color="grey"
            block
            class="mb-4"
            style="font-size: 18px; padding: 12px;"
            @click="guest"
          >
             
          </v-btn>
      </v-card>
    </v-container>
  </template>
  
  <script>
  export default {
    data() {
      return {

        // user  
        userId: '', //  
        password: '', //  
        
        
        // template 
        showPassword: false,  //    
        rules: {              //     
        required: value => !!value || '  .',
        minLength: value => !!(value && value.length >= 6) || ' 6  .',
      },


      //  
      response : null,
      };
    },
    methods: {
      //  
      async login() {

      //     
      if(this.rules.required(this.userId) !== true || this.rules.minLength(this.userId) !== true){
        this.$toast.error("   .");
        return;
      }
        
      //     
      if(this.rules.required(this.password) !== true || this.rules.minLength(this.password) !== true){
        this.$toast.error("   .");
        return;
      }

      //  
      const USER = {
        userId : this.userId,
        password : this. password
      }

      // http 
      try{
        
        //  (http )
        this.response = await this.$axios.post("/api/security/login", USER);
        
        //    
        localStorage.setItem("token", this.response.headers["authorization"]);

        //           
        localStorage.setItem('first', "first");

        //      
        localStorage.setItem('userId', this.response.data.userId);

        //  
        this.$router.push('/home');
      }
      
      // http   
      catch(error){
        if(error.response.status === 401){
          this.$toast.error("  .");
          localStorage.clear();
          return;
        }

        this.$toast.error("  .  .");

      }

      },

      //  
      signUp() {
        this.$router.push('/AddMember');
      },
      //   
      guest() {
        this.$router.push('/Home');

      },
    },

    created(){
      if (localStorage.getItem('token')){
        if(confirm("   .  ?")){
          localStorage.clear();
        }
        else{
          this.$router.push('/home');
        }
      }
      }
  };
  </script>
  
  <style scoped>
  </style>
  