import Vue from 'vue'
import App from './App.vue'
import router from './router';
import vuetify from './plugins/vuetify';
import axios from 'axios';
import Toast from 'vue-toastification';
import 'vue-toastification/dist/index.css';
import Navigation from '@/components/NavigationComponent.vue'

Vue.component('NavigationComponent', Navigation);


Vue.config.productionTip = false


let isToastCooldown = false;
Vue.use(Toast, {
  filterBeforeCreate: (toast) => {
    if (isToastCooldown) {
      return false; //      
    }
    isToastCooldown = true;
    setTimeout(() => {
      isToastCooldown = false; // 5     
    }, 1500);
    return toast; //    
  },
});

//   
Vue.prototype.$checkToken = async function(){


  //  
  const token = localStorage.getItem('token');
  console.log(token)

  //     
  if(!token){

    //  
    alert("     .");
    
    //  URL   
    if (this.$route.name !== 'home'){
      alert("  .");
      this.$router.push("Login");
      return;
    }

    //     
    if(confirm("  ?")){
      this.$router.push("Login");
      return;
    }

    return;

  }
  
  //     

  // http 
  try{

    // token (http)
    const response = await this.$axios.post("/api/security/validate", null, 
      {
        headers : {Authorization: token}
    });

    //     (  )
    if (response.status !== 200){
    alert("     .");
    this.$router.push("Login");
      return;
    }

    //    
    return true;

  }

  // http   
  catch(error){

    //      
    if(error.response.status === 401){
      this.$toast.error(error.response.data);
      localStorage.clear();
      this.$router.push("/Login");
      return false;
    }

    //    
    this.$toast.error("  .  .");
  }
  


}


Vue.prototype.$axios = axios.create({
  baseURL: 'http://localhost:8080'
})

new Vue({
  vuetify,
  router,
  render: h => h(App),
}).$mount('#app')
