<template>
    <v-container class="d-flex justify-center align-center" style="min-height: 100vh; background-color: #fef3f3;">
      <v-card class="pa-8" max-width="500">
        <v-card-title class="text-h5 text-center font-weight-bold"> </v-card-title>
        <v-card-text>
          <p class="text-center" style="font-size: 16px; color: #b71c1c;">
               ? <br/>
               .
          </p>
          <v-form ref="form" lazy-validation>
            <!--   -->
            <v-text-field
              v-model="userId"
              label=""
              outlined
              clearable
              dense
              class="mt-6"
              :rules="[rules.required, rules.minLength]"
              style="font-size: 18px;"
            >
            </v-text-field>
          </v-form>
        </v-card-text>
  
        <!--   -->
          <v-btn
            color="error"
            block
            class="mb-4"
            style="font-size: 18px; padding: 12px;"
            @click="RemoveMember"
          >
             
          </v-btn>
          <v-btn
            color="primary"
            block
            style="font-size: 18px; padding: 12px;"
            @click="cancel"
          >
            
          </v-btn>
      </v-card>
    </v-container>
  </template>
  
  <script>
  export default {
    data() {
      return {

      //   
      userId : '',

      // template  
      rules: {              //     
      required: value => !!value || '  .',
      minLength: value => !!(value && value.length >= 6) || ' 6  .',
    },

      //   
      response : null,


      };
    },
    methods: {

      //  
      async RemoveMember() {


        if(localStorage.getItem("userId") !== this.userId){
          this.$toast.error("  .");
          return;
        }


        if(confirm(" ?")){
        
        //     
        if(this.rules.required(this.userId) === true && this.rules.minLength(this.userId) === true){
          
          //  
          const UserId = {
            userId : this.userId
          }

          // http 
          try{
  
            //  (http )
            this.response = await this.$axios.post("/api/user/RemoveUser", UserId);


            // //        
            if(this.response.status !== 200){
              this.$toast.error("   .");
              return;
            }

            //       

            //   
            this.$toast.info(this.response.data.message);

            //   
            localStorage.clear();

            alert("    .");

            this.$router.push("/Login");
            

          }
          // http   
          catch(error){
            this.$toast.error("  .  .");
          }
        }
        
        //     
        else{
          this.$toast.error("   .");
        }
      }
      },




      //   
      cancel() {
        if (confirm("  ?")) {
        // (OK)   
        this.$router.push("/home");
        }
      },
    },

    created(){
      //  
      this.$checkToken();
    }
  };
  </script>
  
  <style scoped>
  p {
    margin: 0;
  }
  </style>
  