<template>
    <v-container class="d-flex justify-center align-center" style="min-height: 100vh; background-color: #f4f4f4;">
      <v-card class="pa-8" max-width="500">
        <v-card-title class="text-h5 text-center font-weight-bold"></v-card-title>
        <v-card-text>
          <p class="text-center" style="font-size: 16px; color: #1a237e;">
              ? <br />
                .
          </p>
        </v-card-text>
  
        <!--   -->
          <v-btn
            color="error"
            block
            class="mb-4"
            style="font-size: 18px; padding: 12px; max-width: 300px;"
            @click="logout"
          >
            
          </v-btn>
          <v-btn
            color="primary"
            block
            style="font-size: 18px; padding: 12px; max-width: 300px;"
            @click="cancel"
          >
            
          </v-btn>
      </v-card>
    </v-container>
  </template>
  
  <script>
  export default {
    methods: {
      //  
      async logout() {
        if (confirm(" ?")) {

            //   
            localStorage.clear();
  
            //   
            this.$toast.info(" .");
  
            //   
            this.$router.push("/login");

        }
      },
  
      //     
      cancel() {
        if (confirm("  ?")) {
          this.$router.push("/home");
        }
      },
    },

    created(){
      //  
      this.$checkToken();
    }
  };
  </script>
  
  <style scoped>
  p {
    margin: 0;
  }
  </style>
  