<template>
  <v-container class="d-flex justify-center align-center" style="min-height: 100vh; background-color: #f0f4f8;">
    <v-card class="pa-8" max-width="600">
      <v-card-title class="text-h5 text-center font-weight-bold"></v-card-title>
      <v-card-text>
        <v-form ref="form" lazy-validation>
          <!--   -->
          <v-text-field
            v-model="userId"
            label=""
            outlined
            clearable
            dense
            class="mt-6"
            :rules="[rules.required, rules.minLength]"
            style="font-size: 18px;"
          >
            <template #append>
              <v-btn
                text
                color="primary"
                @click="checkId"
                style="font-size: 16px; padding: 0 16px;"
              > </v-btn>
            </template>
          </v-text-field>

          <!--   -->
          <v-text-field
            v-model="password"
            label=""
            outlined
            clearable
            dense
            class="mt-6"
            :type="showPassword ? 'text' : 'password'"
            :rules="[rules.required, rules.minLength]"
            style="font-size: 18px;"
          >
            <template #append>
              <v-btn
                icon
                @click="showPassword = !showPassword"
              >
                <v-icon large>{{ showPassword ? 'mdi-eye-off' : 'mdi-eye' }}</v-icon>
              </v-btn>
            </template>
          </v-text-field>
        </v-form>
      </v-card-text>
      <v-card-actions class="d-flex justify-center">
        <v-btn 
          color="success" 
          block 
          @click="AddMember"
          style="font-size: 18px; padding: 12px;"
        >
          
        </v-btn>
      </v-card-actions>
    </v-card>


  </v-container>
</template>

<script>
export default {
  data() {
    return {
      // user  
      userId: '',
      password: '',
      
      // template 
      showPassword: false,  //   
      rules: {              //     
        required: value => !!value || '  .',
        minLength: value => !!(value && value.length >= 6) || ' 6  .',
      },

      // http  
      response : null,

      //    
      check : false, //  
      nowCheckId : '' //    


    };
  },
  methods: {
    //   
    async checkId() {      
      //   

      //     
      if (this.rules.required(this.userId) === true && this.rules.minLength(this.userId) === true) {
        
        //  
        const CHECKID = {
          userId : this.userId
        }

        // http 
        try{

          //   (http )
          this.response = await this.$axios.post("/api/user/checkId", CHECKID)

          //             
          if(this.response.data.status === "NO"){
            this.$toast.error(this.response.data.message);
            this.check = false; //    
            return;
          }

          //             
          this.$toast.success(this.response.data.message);
          this.check = true;  //    
          this.nowCheckId = this.userId; //     

        }

        // http   
        catch(error){
          this.$toast.error("  .  .");
        }

      } 
      //      
      else {
        this.$toast.error('   .');
      }
    },




    //   
    async AddMember() {

      //   

      //     
      if(this.check === false){
        this.$toast.error("  .");
        return;
      }

      //     
      if(this.rules.required(this.password) !== true || this.rules.minLength(this.password) !== true){
        this.$toast.error("   .");
        return;
      }

      //        
      if(this.nowCheckId !== this.userId){
        this.$toast.error(`      .\n    : ${this.nowCheckId}`);
        return;
      }


      //  
      const USER = {
        userId : this.userId,
        password : this.password
      }

      // http 
      try{

        //   (http )
        this.response = await this.$axios.post('/api/user/AddUser', USER);

        //   
        this.$toast.success(this.response.data);
        this.$router.push('/Login');
      }

      // http   
      catch(error){
        this.$toast.error("  .  .");
      }

    },
  },
};
</script>

<style scoped>
.v-btn {
  font-size: 16px;
  text-transform: none;
}

.v-text-field {
  font-size: 18px;
}

.v-card {
  border-radius: 12px;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1);
}
</style>
